import React, {Component} from 'react';
import {Card, Row, Col, Input, Button,message} from 'antd';
import {postAction} from '../../../axios'
export default class ChuLiDaoBiaoPeiZhi extends Component {

    constructor(props){
        super(props);
        this.state = {
            //失联预警
            shilianRedRed:"",
            shilianOrange: "",
            shilianYellow: "",
            shilianBlue: "",

            //挂科预警
            guakeRed:"",
            guakeOrange: "",
            guakeYellow: "",
            guakeBlue: "",

            //上网预警
            shangwangRed: "",
            shangwangOrange: "",
            shangwangYellow: "",
            shangwangBlue: "",

            //消费预警
            xiaofeiRed: "",
            xiaofeiOrange: "",
            xiaofeiYellow: "",
            xiaofeiBlue: "",

            //夜归预警
            yeguiRed: "",
            yeguiOrange: "",
            yeguiYellow: "",
            yeguiBlue: "",

            //逃课预警
            taokeRed: "",
            taokeOrange: "",
            taokeYellow: "",
            taokeBlue: "",

            configId:'',
            flag:false  //更新是false
        }
    }

    onChange(type,e) {
        this.setState({
            [type]:e.target.value || ''
        })
    }
    componentDidMount = () => {
        postAction(
            '/bigdata/alarmHandleRatio/findAll',
            {}
        ).then(res=>{
            if(res.success){
                if(res.obj[0]){
                    this.setState({
                        shilianRed: res.obj[0].alarmMissRed,
                        shilianOrange: res.obj[0].alarmMissOrange,
                        shilianYellow: res.obj[0].alarmMissYellow,
                        shilianBlue: res.obj[0].alarmMissBlue,   // 失联预警
                        guakeRed: res.obj[0].alarmFailRed,
                        guakeOrange: res.obj[0].alarmFailOrange,
                        guakeYellow: res.obj[0].alarmFailYellow,
                        guakeBlue: res.obj[0].alarmFailBlue,     // 挂科预警
                        shangwangRed: res.obj[0].alarmNetworkRed,
                        shangwangOrange: res.obj[0].alarmNetworkOrange,
                        shangwangYellow: res.obj[0].alarmNetworkYellow,
                        shangwangBlue: res.obj[0].alarmNetworkBlue,  // 上网预警
                        xiaofeiRed: res.obj[0].alarmConsumeRed,
                        xiaofeiOrange: res.obj[0].alarmConsumeOrange,
                        xiaofeiYellow: res.obj[0].alarmConsumeYellow,
                        xiaofeiBlue: res.obj[0].alarmConsumeBlue,   // 消费预警
                        yeguiRed: res.obj[0].alarmBackRed,
                        yeguiOrange: res.obj[0].alarmBackOrange,
                        yeguiYellow: res.obj[0].alarmBackYellow,
                        yeguiBlue: res.obj[0].alarmBackBlue,      // 夜归预警
                        taokeRed: res.obj[0].alarmCourseRed,
                        taokeOrange: res.obj[0].alarmConsumeOrange,
                        taokeYellow: res.obj[0].alarmConsumeYellow,
                        taokeBlue: res.obj[0].alarmConsumeBlue,   // 逃课预警
                        configId: res.obj[0].id,
                        flag: false
                    })
                }else{
                    this.setState({
                        flag:true
                    })
                }

            }else{
                this.setState({
                    flag:true
                })
            }
        })
    }
    // 点击保存
    saveAction=()=>{
        // 失联预警
        if (this.state.shilianRed === 0 || this.state.shilianRed === "0") {
            message.warning('失联红色预警配置不能为零');
            return
        }
        if (this.state.shilianOrange === 0 || this.state.shilianOrange === "0") {
            message.warning('失联橙色预警配置不能为零');
            return
        }
        if (this.state.shilianYellow === 0 || this.state.shilianYellow === "0") {
            message.warning('失联黄色预警配置不能为零');
            return
        }
        if (this.state.shilianBlue === 0 || this.state.shilianBlue === "0") {
            message.warning('失联蓝色预警配置不能为零');
            return
        }
        // 挂科预警
        if (this.state.guakeRed === 0 || this.state.guakeRed === "0") {
            message.warning('挂科红色预警配置不能为零');
            return
        }
        if (this.state.guakeOrange === 0 || this.state.guakeOrange === "0") {
            message.warning('挂科橙色预警配置不能为零');
            return
        }
        if (this.state.guakeYellow === 0 || this.state.guakeYellow === "0") {
            message.warning('挂科黄色预警配置不能为零');
            return
        }
        if (this.state.guakeBlue === 0 || this.state.guakeBlue === "0") {
            message.warning('挂科蓝色预警配置不能为零');
            return
        }
        // 上网预警
        if (this.state.shangwangRed === 0 || this.state.shangwangRed === "0") {
            message.warning('上网红色预警配置不能为零');
            return
        }
        if (this.state.shangwangOrange === 0 || this.state.shangwangOrange === "0") {
            message.warning('上网橙色预警配置不能为零');
            return
        }
        if (this.state.shangwangYellow === 0 || this.state.shangwangYellow === "0") {
            message.warning('上网黄色预警配置不能为零');
            return
        }
        if (this.state.shangwangBlue === 0 || this.state.shangwangBlue === "0") {
            message.warning('上网蓝色预警配置不能为零');
            return
        }
        // 消费预警
        if (this.state.xiaofeiRed === 0 || this.state.xiaofeiRed === "0") {
            message.warning('消费红色预警配置不能为零');
            return
        }
        if (this.state.xiaofeiOrange === 0 || this.state.xiaofeiOrange === "0") {
            message.warning('消费橙色预警配置不能为零');
            return
        }
        if (this.state.xiaofeiYellow === 0 || this.state.xiaofeiYellow === "0") {
            message.warning('消费黄色预警配置不能为零');
            return
        }
        if (this.state.xiaofeiBlue === 0 || this.state.xiaofeiBlue === "0") {
            message.warning('消费蓝色预警配置不能为零');
            return
        }
        // 夜归预警
        if (this.state.yeguiRed === 0 || this.state.yeguiRed === "0") {
            message.warning('夜归红色预警配置不能为零');
            return
        }
        if (this.state.yeguiOrange === 0 || this.state.yeguiOrange === "0") {
            message.warning('夜归橙色预警配置不能为零');
            return
        }
        if (this.state.yeguiYellow === 0 || this.state.yeguiYellow === "0") {
            message.warning('夜归黄色预警配置不能为零');
            return
        }
        if (this.state.yeguiBlue === 0 || this.state.yeguiBlue === "0") {
            message.warning('夜归蓝色预警配置不能为零');
            return
        }
        // 逃课预警
        if (this.state.taokeRed === 0 || this.state.taokeRed === "0") {
            message.warning('逃课红色预警配置不能为零');
            return
        }
        if (this.state.taokeOrange === 0 || this.state.taokeOrange === "0") {
            message.warning('逃课橙色预警配置不能为零');
            return
        }
        if (this.state.taokeYellow === 0 || this.state.taokeYellow === "0") {
            message.warning('逃课黄色预警配置不能为零');
            return
        }
        if (this.state.taokeBlue === 0 || this.state.taokeBlue === "0") {
            message.warning('逃课蓝色预警配置不能为零');
            return
        }

        if(this.state.flag){
            let param={
                alarmMissRed: this.state.shilianRed,
                alarmMissOrange: this.state.shilianOrange,
                alarmMissYellow: this.state.shilianYellow,
                alarmMissBlue: this.state.shilianBlue,    // 失联预警
                alarmFailRed: this.state.guakeRed,
                alarmFailOrange: this.state.guakeOrange,
                alarmFailYellow: this.state.guakeYellow,
                alarmFailBlue: this.state.guakeBlue,      // 挂科预警
                alarmNetworkRed: this.state.shangwangRed,
                alarmNetworkOrange: this.state.shangwangOrange,
                alarmNetworkYellow: this.state.shangwangYellow,
                alarmNetworkBlue: this.state.shangwangBlue,  // 上网预警
                alarmConsumeRed: this.state.xiaofeiRed,
                alarmConsumeOrange: this.state.xiaofeiOrange,
                alarmConsumeYellow: this.state.xiaofeiYellow,
                alarmConsumeBlue: this.state.xiaofeiBlue,   // 消费预警
                alarmBackRed: this.state.yeguiRed,
                alarmBackOrange: this.state.yeguiOrange,
                alarmBackYellow: this.state.yeguiYellow,
                alarmBackBlue: this.state.yeguiBlue,    // 夜归预警
                alarmCourseRed: this.state.taokeRed,
                alarmCourseOrange: this.state.taokeOrange,
                alarmCourseYellow: this.state.taokeYellow,
                alarmCourseBlue: this.state.taokeBlue,    // 逃课预警
            }
            // 新增
            postAction(
                '/bigdata/alarmHandleRatio/save',
                param
            ).then(res=>{
                if(res.success){
                    message.success('保存成功')
                }else{
                    message.error(res.obj)
                }
            })
        }else{
            // 更新
            let param={
                alarmMissRed: this.state.shilianRed,
                alarmMissOrange: this.state.shilianOrange,
                alarmMissYellow: this.state.shilianYellow,
                alarmMissBlue: this.state.shilianBlue,    // 失联预警
                alarmFailRed: this.state.guakeRed,
                alarmFailOrange: this.state.guakeOrange,
                alarmFailYellow: this.state.guakeYellow,
                alarmFailBlue: this.state.guakeBlue,      // 挂科预警
                alarmNetworkRed: this.state.shangwangRed,
                alarmNetworkOrange: this.state.shangwangOrange,
                alarmNetworkYellow: this.state.shangwangYellow,
                alarmNetworkBlue: this.state.shangwangBlue,  // 上网预警
                alarmConsumeRed: this.state.xiaofeiRed,
                alarmConsumeOrange: this.state.xiaofeiOrange,
                alarmConsumeYellow: this.state.xiaofeiYellow,
                alarmConsumeBlue: this.state.xiaofeiBlue,   // 消费预警
                alarmBackRed: this.state.yeguiRed,
                alarmBackOrange: this.state.yeguiOrange,
                alarmBackYellow: this.state.yeguiYellow,
                alarmBackBlue: this.state.yeguiBlue,    // 夜归预警
                alarmCourseRed: this.state.taokeRed,
                alarmCourseOrange: this.state.taokeOrange,
                alarmCourseYellow: this.state.taokeYellow,
                alarmCourseBlue: this.state.taokeBlue,    // 逃课预警
                id:this.state.configId
            }
            // 新增
            postAction(
                '/bigdata/alarmHandleRatio/update',
                param
            ).then(res=>{
                if(res.success){
                    message.success('保存成功')
                }else{
                    message.error(res.obj)
                }
            })
        }
    }
    render () {
        return (
            <Card
                title={<div>预警处理达标配置</div>}
                bodyStyle={{height:"100%"}}
            >
                {/*失联预警*/}
                <Row>
                    <Col lg={3} style={{lineHeight:"32px", textAlign:"right"}}>
                        <span>失联预警：</span>
                    </Col>
                    <Col lg={4} style={{textAlign:"right"}}>
                        <span>红色&nbsp;&nbsp;
                            <Input onChange={this.onChange.bind(this,'shilianRed')} value={this.state.shilianRed} style={{display:'inline-block',width:100}}/>
                            &nbsp;&nbsp;</span>
                    </Col>
                    <Col lg={4} style={{textAlign:"right"}}>
                        <span>橙色&nbsp;&nbsp;
                            <Input onChange={this.onChange.bind(this,'shilianOrange')} value={this.state.shilianOrange} style={{display:'inline-block',width:100}}/>
                            &nbsp;&nbsp;</span>
                    </Col>
                    <Col lg={4} style={{textAlign:"right"}}>
                        <span>黄色&nbsp;&nbsp;
                            <Input onChange={this.onChange.bind(this,'shilianYellow')} value={this.state.shilianYellow} style={{display:'inline-block',width:100}}/>
                            &nbsp;&nbsp;</span>
                    </Col>
                    <Col lg={7} style={{textAlign:"right"}}>
                        <span>蓝色&nbsp;&nbsp;
                            <Input onChange={this.onChange.bind(this,'shilianBlue')} value={this.state.shilianBlue} style={{display:'inline-block',width:100}}/>
                            &nbsp;&nbsp;小时内处理则达标</span>
                    </Col>
                </Row>

                {/*挂科预警*/}
                <Row style={{padding:"20px 0px"}}>
                    <Col lg={3} style={{lineHeight:"32px", textAlign:"right"}}>
                        <span>挂科预警：</span>
                    </Col>
                    <Col lg={4} style={{textAlign:"right"}}>
                        <span>红色&nbsp;&nbsp;
                            <Input onChange={this.onChange.bind(this,'guakeRed')} style={{display:'inline-block',width:100}} value={this.state.guakeRed}/>
                            &nbsp;&nbsp;</span>
                    </Col>
                    <Col lg={4} style={{textAlign:"right"}}>
                        <span>橙色&nbsp;&nbsp;
                            <Input onChange={this.onChange.bind(this,'guakeOrange')} value={this.state.guakeOrange} style={{display:'inline-block',width:100}}/>
                            &nbsp;&nbsp;</span>
                    </Col>
                    <Col lg={4} style={{textAlign:"right"}}>
                        <span>黄色&nbsp;&nbsp;
                            <Input onChange={this.onChange.bind(this,'guakeYellow')} value={this.state.guakeYellow} style={{display:'inline-block',width:100}}/>
                            &nbsp;&nbsp;</span>
                    </Col>
                    <Col lg={7} style={{textAlign:"right"}}>
                        <span>蓝色&nbsp;&nbsp;
                            <Input onChange={this.onChange.bind(this,'guakeBlue')} value={this.state.guakeBlue} style={{display:'inline-block',width:100}}/>
                            &nbsp;&nbsp;小时内处理则达标</span>
                    </Col>
                </Row>

                {/*上网预警*/}
                <Row>
                    <Col lg={3} style={{lineHeight:"32px", textAlign:"right"}}>
                        <span>上网预警：</span>
                    </Col>
                    <Col lg={4} style={{textAlign:"right"}}>
                        <span>红色&nbsp;&nbsp;
                            <Input onChange={this.onChange.bind(this,'shangwangRed')} style={{display:'inline-block',width:100}} value={this.state.shangwangRed}/>
                            &nbsp;&nbsp;</span>
                    </Col>
                    <Col lg={4} style={{textAlign:"right"}}>
                        <span>橙色&nbsp;&nbsp;
                            <Input onChange={this.onChange.bind(this,'shangwangOrange')} value={this.state.shangwangOrange} style={{display:'inline-block',width:100}}/>
                            &nbsp;&nbsp;</span>
                    </Col>
                    <Col lg={4} style={{textAlign:"right"}}>
                        <span>黄色&nbsp;&nbsp;
                            <Input onChange={this.onChange.bind(this,'shangwangYellow')} value={this.state.shangwangYellow} style={{display:'inline-block',width:100}}/>
                            &nbsp;&nbsp;</span>
                    </Col>
                    <Col lg={7} style={{textAlign:"right"}}>
                        <span>蓝色&nbsp;&nbsp;
                            <Input onChange={this.onChange.bind(this,'shangwangBlue')} value={this.state.shangwangBlue} style={{display:'inline-block',width:100}}/>
                            &nbsp;&nbsp;小时内处理则达标</span>
                    </Col>
                </Row>

                {/*消费预警*/}
                <Row style={{padding:"20px 0px"}}>
                    <Col lg={3} style={{lineHeight:"32px", textAlign:"right"}}>
                        <span>消费预警：</span>
                    </Col>
                    <Col lg={4} style={{textAlign:"right"}}>
                        <span>红色&nbsp;&nbsp;
                            <Input onChange={this.onChange.bind(this,'xiaofeiRed')} style={{display:'inline-block',width:100}} value={this.state.xiaofeiRed}/>
                            &nbsp;&nbsp;</span>
                    </Col>
                    <Col lg={4} style={{textAlign:"right"}}>
                        <span>橙色&nbsp;&nbsp;
                            <Input onChange={this.onChange.bind(this,'xiaofeiOrange')} value={this.state.xiaofeiOrange} style={{display:'inline-block',width:100}}/>
                            &nbsp;&nbsp;</span>
                    </Col>
                    <Col lg={4} style={{textAlign:"right"}}>
                        <span>黄色&nbsp;&nbsp;
                            <Input onChange={this.onChange.bind(this,'xiaofeiYellow')} value={this.state.xiaofeiYellow} style={{display:'inline-block',width:100}}/>
                            &nbsp;&nbsp;</span>
                    </Col>
                    <Col lg={7} style={{textAlign:"right"}}>
                        <span>蓝色&nbsp;&nbsp;
                            <Input onChange={this.onChange.bind(this,'xiaofeiBlue')} value={this.state.xiaofeiBlue} style={{display:'inline-block',width:100}}/>
                            &nbsp;&nbsp;小时内处理则达标</span>
                    </Col>
                </Row>

                {/*夜归预警*/}
                <Row>
                    <Col lg={3} style={{lineHeight:"32px", textAlign:"right"}}>
                        <span>夜归预警：</span>
                    </Col>
                    <Col lg={4} style={{textAlign:"right"}}>
                        <span>红色&nbsp;&nbsp;
                            <Input onChange={this.onChange.bind(this,'yeguiRed')} style={{display:'inline-block',width:100}} value={this.state.yeguiRed}/>
                            &nbsp;&nbsp;</span>
                    </Col>
                    <Col lg={4} style={{textAlign:"right"}}>
                        <span>橙色&nbsp;&nbsp;
                            <Input onChange={this.onChange.bind(this,'yeguiOrange')} value={this.state.yeguiOrange} style={{display:'inline-block',width:100}}/>
                            &nbsp;&nbsp;</span>
                    </Col>
                    <Col lg={4} style={{textAlign:"right"}}>
                        <span>黄色&nbsp;&nbsp;
                            <Input onChange={this.onChange.bind(this,'yeguiYellow')} value={this.state.yeguiYellow} style={{display:'inline-block',width:100}}/>
                            &nbsp;&nbsp;</span>
                    </Col>
                    <Col lg={7} style={{textAlign:"right"}}>
                        <span>蓝色&nbsp;&nbsp;
                            <Input onChange={this.onChange.bind(this,'yeguiBlue')} value={this.state.yeguiBlue} style={{display:'inline-block',width:100}}/>
                            &nbsp;&nbsp;小时内处理则达标</span>
                    </Col>
                </Row>

                {/*逃课预警*/}
                <Row style={{padding:"20px 0px"}}>
                    <Col lg={3} style={{lineHeight:"32px", textAlign:"right"}}>
                        <span>逃课预警：</span>
                    </Col>
                    <Col lg={4} style={{textAlign:"right"}}>
                        <span>红色&nbsp;&nbsp;
                            <Input onChange={this.onChange.bind(this,'taokeRed')} style={{display:'inline-block',width:100}} value={this.state.taokeRed}/>
                            &nbsp;&nbsp;</span>
                    </Col>
                    <Col lg={4} style={{textAlign:"right"}}>
                        <span>橙色&nbsp;&nbsp;
                            <Input onChange={this.onChange.bind(this,'taokeOrange')} value={this.state.taokeOrange} style={{display:'inline-block',width:100}}/>
                            &nbsp;&nbsp;</span>
                    </Col>
                    <Col lg={4} style={{textAlign:"right"}}>
                        <span>黄色&nbsp;&nbsp;
                            <Input onChange={this.onChange.bind(this,'taokeYellow')} value={this.state.taokeYellow} style={{display:'inline-block',width:100}}/>
                            &nbsp;&nbsp;</span>
                    </Col>
                    <Col lg={7} style={{textAlign:"right"}}>
                        <span>蓝色&nbsp;&nbsp;
                            <Input onChange={this.onChange.bind(this,'taokeBlue')} value={this.state.taokeBlue} style={{display:'inline-block',width:100}}/>
                            &nbsp;&nbsp;小时内处理则达标</span>
                    </Col>
                </Row>

                <Row>
                    <Col lg={24}>
                        <span>注：为空则不纳入统计</span>
                    </Col>
                </Row>

                <Row style={{paddingTop:"100px"}}>
                    <div style={{textAlign:"center"}}>
                        <Button
                            size="large"
                            type="primary"
                            style={{marginRight:20}}
                            onClick={this.saveAction}
                        >
                            {'保 存'}
                        </Button>                        
                    </div>
                </Row>
            </Card>
        )
    }
}


